
"use client";

import { Button } from "@/components/ui/button";
import { useTrackingStore } from "@/store/tracking-store";
import { RefreshCw, Tag, Search, TrafficCone } from "lucide-react";

export default function TrackingTopBar() {
  const {
    autoRefresh,
    toggleAutoRefresh,
    showCallsigns,
    toggleCallsigns,
  } = useTrackingStore();

  return (
    <div className="absolute top-4 left-4 z-10 flex gap-2 bg-background/80 backdrop-blur-sm p-2 rounded-lg shadow-md border">
      <Button onClick={toggleAutoRefresh} variant="outline" size="sm">
        <RefreshCw className={`mr-2 h-4 w-4 ${autoRefresh ? 'animate-spin' : ''}`} />
        {autoRefresh ? "Auto Refresh ON" : "Auto Refresh OFF"}
      </Button>
      <Button onClick={toggleCallsigns} variant="outline" size="sm">
        <Tag className="mr-2 h-4 w-4" />
        {showCallsigns ? "Hide Callsigns" : "Show Callsigns"}
      </Button>
      <Button variant="outline" size="sm">
        <Search className="mr-2 h-4 w-4" />
        Find Nearest
      </Button>
      <Button variant="outline" size="sm">
        <TrafficCone className="mr-2 h-4 w-4" />
        Traffic
      </Button>
    </div>
  );
}
