
"use client";

import { Vehicle } from "@/types/tracking";
import { cn } from "@/lib/utils";
import { useTrackingStore } from "@/store/tracking-store";
import { Car, Bike, Ship, Plane, Bus, Truck } from "lucide-react";
import React from "react";

const vehicleTypeIcons: Record<Vehicle["type"], React.ElementType> = {
    COACH: Bus,
    BIKE: Bike,
    BOAT: Ship,
    ECO: Car,
    ESTATE: Car,
    EXECUTIVE: Car,
    MPV: Bus,
    PUSH_BIKE: Bike,
    SALOON: Car,
    SHUTTLE_BUS: Bus,
    VAN: Truck,
};


export default function VehicleMarker({ vehicle }: { vehicle: Vehicle }) {
  const { selectVehicle, selectedVehicle, showCallsigns } = useTrackingStore();
  
  const isSelected = selectedVehicle?.id === vehicle.id;
  const Icon = vehicleTypeIcons[vehicle.type] || Car;

  return (
    <button
      onClick={() => selectVehicle(vehicle)}
      className={cn(
        "flex items-center gap-1 rounded-full border-2 text-xs font-semibold shadow-lg transition-all duration-200",
        "h-8 pr-2",
        {
          "bg-green-500 border-green-700 text-white": vehicle.status === "POB",
          "bg-blue-500 border-blue-700 text-white": vehicle.status === "ON_ROUTE",
          "bg-gray-400 border-gray-600 text-white": vehicle.status === "EMPTY",
          "bg-yellow-400 border-yellow-600 text-black": vehicle.status === "RECEIVED",
          "bg-purple-500 border-purple-700 text-white": vehicle.status === "ACCEPTED",
          "bg-orange-500 border-orange-700 text-white": vehicle.status === "ARRIVED",
        },
        isSelected ? "scale-125 z-10" : "hover:scale-110"
      )}
    >
        <div className="bg-white/30 rounded-full p-1 h-full aspect-square flex items-center justify-center">
            <Icon className="h-4 w-4" />
        </div>
        {showCallsigns && <span>{vehicle.callsign}</span>}
    </button>
  );
}
