
"use client";

import { useTrackingStore } from "@/store/tracking-store";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { Button } from "../ui/button";
import { Phone, MessageSquare, Briefcase, Clock } from "lucide-react";

export default function DriverCard() {
  const { selectedVehicle, selectVehicle } = useTrackingStore();

  if (!selectedVehicle) {
    return (
        <div className="flex flex-col items-center justify-center h-full text-center text-muted-foreground p-4">
            <Briefcase className="h-12 w-12 mb-4" />
            <h3 className="font-semibold">No Vehicle Selected</h3>
            <p className="text-sm">Click on a vehicle on the map to see its details.</p>
        </div>
    )
  }

  const { driver } = selectedVehicle;

  return (
    <div className="p-4 h-full flex flex-col">
        <Card>
            <CardHeader>
                <div className="flex justify-between items-start">
                    <div>
                        <CardTitle>{selectedVehicle.plateNumber}</CardTitle>
                        <CardDescription>{selectedVehicle.callsign} - {selectedVehicle.type}</CardDescription>
                    </div>
                    <Button variant="ghost" size="sm" onClick={() => selectVehicle(undefined)}>Close</Button>
                </div>
            </CardHeader>
            <CardContent className="space-y-4">
                <div className="text-sm">
                    <p><strong>Status:</strong> {selectedVehicle.status}</p>
                    <p><strong>Speed:</strong> {selectedVehicle.speed ?? "--"} km/h</p>
                    <p><strong>Fuel:</strong> {selectedVehicle.fuel ?? "--"}%</p>
                    <p><strong>Battery:</strong> {selectedVehicle.battery ?? "--"}%</p>
                </div>

                {driver && (
                    <>
                        <Separator />
                        <div>
                            <h4 className="font-semibold mb-2">Driver Details</h4>
                            <div className="text-sm space-y-1">
                                <p><strong>Name:</strong> {driver.name}</p>
                                <p><strong>Phone:</strong> {driver.phone}</p>
                                <p><strong>Shift End:</strong> {driver.shiftEnd}</p>
                            </div>
                        </div>
                    </>
                )}

                <Separator />
                
                <div className="grid grid-cols-2 gap-2">
                    <Button variant="outline"><Phone className="mr-2 h-4 w-4"/> Call</Button>
                    <Button variant="outline"><MessageSquare className="mr-2 h-4 w-4"/> Message</Button>
                </div>
            </CardContent>
        </Card>
        
        <div className="flex-grow p-4 mt-4 border rounded-lg bg-muted/50">
            <h4 className="font-semibold mb-2 flex items-center"><Clock className="mr-2 h-4 w-4"/> Activity Log</h4>
            <p className="text-sm text-muted-foreground">No recent activity.</p>
        </div>

    </div>
  );
}
